.sidebar{
    color: white;
    background-color: var(--slack-color);
    min-width: 250px;
    border-top: 1px solid #49274b;
    
}
.sidebar__header{
    display: flex;
    padding: 13px;
    border-bottom: 1px solid #49274b;
    padding-bottom: 10px;

}
.sidebar__header>.MuiSvgIcon-root{
    padding: 8px;
    background-color: white;
    color: #49274b;
    border-radius: 999px;
    font-size: 18px;
}
.sidebar__info{
    flex: 1;
}
.sidebar__info>h2{
    font-size: 15px;
    font-weight: 900;
    margin-bottom: 5px;
}

.sidebar__info>h3{
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: 400;
}
.sidebar__info>h3>.MuiSvgIcon-root{
    font-size: 14px;
    margin-top: 1px;
    margin-right: 2px;
    color: green;
}
hr{
    color: grey;
}